import React from 'react'
import { Route } from 'react-router-dom'
import config from '../../config'

// Components
import Developer from './Authors/Developer'
import Redactor from './Authors/Redactor'
import Writer from './Authors/Writer'
import Default from './Authors/Default'

const authorsPage = props => {
  return (
  <React.StrictMode>
    <Route exact path={`/${config.menu.authors}/id=1`} component={Developer}/>
    <Route exact path={`/${config.menu.authors}/id=2`} component={Redactor}/>
    <Route exact path={`/${config.menu.authors}/id=3`} component={Writer}/>
    <Route exact path={`/${config.menu.authors}`} component={Default} config={ config }/>
  </React.StrictMode>
  )
}


export default authorsPage