import React from "react";
import { BrowserRouter, Route } from "react-router-dom";

// CSS Styles
import "../css/Main.css";

// Components
import Menu from './Menu'
import CompactMenu from './CompactMenu'

import homePage from "./Pages/homePage"
import contentPage from "./Pages/contentPage"
import authorsPage from "./Pages/authorsPage"
import aboutPage from "./Pages/aboutPage"

const Main = props => {
  return (
    <BrowserRouter>
      <React.StrictMode>
        <main>
          <CompactMenu { ...props } />
          <Menu  { ...props } />
          <Route exact path={`/${props.menu.home}`} component={homePage} />
          <Route exact path={`/${props.menu.content}`} component={contentPage} />
          <Route exact path={`/${props.menu.authors}*`} component={authorsPage} />
          <Route exact path={`/${props.menu.about}`} component={aboutPage} />
        </main>
      </React.StrictMode>
    </BrowserRouter>
  );
}

export default Main;
